"use client";
import React, { useEffect } from "react";
import { signIn, signOut, useSession } from "next-auth/react";
import {
  Menubar,
  MenubarContent,
  MenubarItem,
  MenubarMenu,
  MenubarTrigger,
} from "@/components/ui/menubar";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";

const Auth = () => {
  const { data: session, status } = useSession({ required: true });

  if (session) {
    return (
      <>
        <h1>{status}</h1>
        {JSON.stringify(session)}
        <br />
        <button
          onClick={() => {
            signOut();
          }}
        >
          Sign out
        </button>
      </>
    );
  }

  return (
    <>
      Not Signed in
      <br />
      <button onClick={() => signIn()}>Sign In</button>
    </>
  );
};

const NavMenu = () => {
  const { data: session, status } = useSession({ required: true });
  return (
    <div>
      <Auth />
      {session && (
        <Menubar className="rounded-full border-none">
          <MenubarMenu>
            <MenubarTrigger className="cursor-pointer p-0 focus:bg-transparent data-[state=open]:bg-transparent">
              <Avatar>
                <AvatarImage
                  src={session.user?.profile_picture ?? undefined}
                  alt={session.user?.name ?? "User"}
                />
                <AvatarFallback>
                  {session.user?.name
                    ?.split(" ")
                    .map((n) => n[0])
                    .join("")}
                </AvatarFallback>
              </Avatar>
            </MenubarTrigger>
            <MenubarContent className="mr-2 mt-1 min-w-[200px]">
              <div className="px-2 py-1.5 text-sm font-medium">
                {session.user?.email}
              </div>
              <MenubarItem
                className="cursor-pointer focus:bg-red-50 focus:text-red-500"
                onClick={() => signOut()}
              >
                Sign out
              </MenubarItem>
            </MenubarContent>
          </MenubarMenu>
        </Menubar>
      )}
    </div>
  );
};

export default NavMenu;
